.country{
    display: flex;
    background:#fff;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 1px 1px 1px 20px rgba(51, 51, 51, 0.075);
}
.image{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
}
.image img{
    width: 150px;
    height: 100px;
}